@import "../utils/Colors.scss";
.navBarContainer {
  position: fixed;
  width: 100%;
  height: 100px;
  background-color: white;
  left: 0;
  z-index: 999;
  display: flex;
  align-items: center;
  .navbar {
    display: flex;
    width: 100%;
    justify-content: space-around;
    background-color: white;
    .col {
      display: flex;
      justify-content: space-between;
      align-items: center;
      .collapseble-button {
        display: none;
      }
    }
    nav {
      .links {
        ul {
          display: flex;
          list-style-type: none;
          align-items: center;
          justify-content: center;
          margin: 0.3rem 2rem;
          color: $lightFontColor;
          li {
            a {
              text-decoration: none;
              font-size: 1.2rem;
              color: $blueColor;
              &:hover {
                color: $buttonPinkColor;
              }
              &:focus {
                color: $blueColor;
              }
              &:active {
                color: $blueColor;
              }
            }
            text-transform: uppercase;
            margin: 0 1rem;
          }
        }
      }
    }
  }
  @media screen and (min-width: 320px) and (max-width: 768px) {
    .navbar.active {
      padding-top: 210px;
      nav {
        display: block;
      }
    }
    .navbar {
      padding: 0 20px;
      flex-direction: column;
      justify-content: flex-start;
      .col {
        .collapseble-button {
          display: block;
          font-size: 4rem;
        }
      }
      nav {
        display: none;
        .links {
          ul {
            flex-direction: column;
            align-items: flex-start;
            margin: 0.3rem 0rem;
            li {
              margin: 2rem 0 0 0;
              &:first-of-type {
                margin-top: 4rem;
              }
              a {
                font-size: x-large;
              }
            }
          }
        }
      }
    }
  }
}
